import { Observable, startWith } from 'rxjs';
import { CommonModule } from '@angular/common';
import { BreakpointObserver } from '@angular/cdk/layout';
import { AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { IPostFull, Post } from '@ApiClient/backend/Post/post';
import { IPostService } from '@ApiClient/services/post';
import { ToService } from '@ApiClient/helpers/observables';
import { Api } from '@ApiClient/backend/api';

import { ImageComponent } from '@ApiClient/components/image/image.component';
import { MessageService } from 'primeng/api';
import { URLS } from '@enums/urls';
import { RouterLink } from '@angular/router';
import { map } from 'rxjs/operators';
import { ClickedOutsideModule } from '../../directives/clickedOutside.module';

@Component({
  selector: 'app-post-card',
  standalone: true,
  imports: [CommonModule, TranslateModule, ImageComponent, RouterLink, ClickedOutsideModule],
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './post-card.component.html',
  styleUrl: './post-card.component.scss',
})
export class PostCardComponent implements OnInit, AfterViewInit {
  @ViewChild('textContainer') textContainer!: ElementRef;
  @Input() post!: IPostFull;
  @Input() isUniquePost: boolean = false;
  showMoreText = false;
  urlThumbnail$!: Observable<string>;
  postService: IPostService;
  showMoreOptionTab: boolean = false;
  isMobileScreen: boolean = false;
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );

  ngAfterViewInit() {
    const textElement = this.textContainer.nativeElement;
    this.showMoreText = textElement.scrollHeight > textElement.clientHeight;
  }

  constructor(
    private toast: MessageService,
    private translate: TranslateService,
    private breakpointObserver: BreakpointObserver,
  ) {
    this.postService = ToService<IPostService>(Post);
  }

  async ngOnInit(): Promise<void> {
    this.setUpImage();
    console.log('Posts: ', this.post);

    this.breakpointObserver.observe(['(max-width: 768px)']).subscribe((res) => {
      this.isMobileScreen = res.matches;
    });
  }

  setUpImage() {
    this.urlThumbnail$ = this.postService.getUrlPostFileThumbnail({
      fileId: this.post?.postFiles[0].fileId,
      userCreatorId: this.post?.userCreatorId,
      id: this.post?.postFiles[0].id,
    });
  }

  async copyLink(id: string) {
    console.log('copyLink');
    await this.copy(`${Api.AppUrl}/${this.translate.currentLang}/${URLS.POST}/${id}`);
  }

  toggleShowMore() {
    this.textContainer.nativeElement.classList.toggle('show-all');
  }

  async copy(msg: string) {
    try {
      await navigator.clipboard.writeText(msg);
      this.toast.add({
        severity: 'success',
        summary: this.translate.instant('SUCCESS.TITLE'),
        detail: this.translate.instant('SUCCESS.URL_COPY'),
      });
    } catch {
      this.toast.add({ severity: 'error', summary: 'Error', detail: this.translate.instant('ERROR.URL_COPY') });
    }
  }

  toggleMoreOption(event?: MouseEvent): void {
    if (event) {
      event.stopPropagation();
    }
    this.showMoreOptionTab = !this.showMoreOptionTab;
  }

  clickedOutside() {
    this.showMoreOptionTab = false;
  }

  protected readonly URLS = URLS;
}
